import axios from 'axios';

export default function (id) {
  const require = new Promise((resolve, reject) => {
    /*axios.get('http://47.94.16.170:3000/playlist/detail',{
      params:{
        id:id
      },
    }).then((data)=> {
      resolve(data);
    })*/
    axios.get('v8/fcg-bin/fcg_v8_toplist_cp.fcg',{
      params:{
        format: 'json',
        inCharset: 'utf8',
        outCharset: 'utf-8',
        notice: 0,
        g_tk: 5381,
        platform: 'h5',
        needNewCode: 1,
        tpl: 3,
        page: 'detail',
        type: 'top',
        topid: id
      },
    }).then((data)=> {
      resolve(data);
    });
  });
  return require;
}



