<template>
  <div class="song-list">
    <div class="top">
      <header class="list-header">
        <i class="icon-back"
           @click="$router.push('/index/findMusic')">
          &#xe60c;
        </i>
        <span>歌单</span>
        <i class="icon-search">&#xe63b;</i>
        <i class="icon-more">&#xe600;</i>
      </header>
      <div class="dics">
        <div class="sheet-pic">
          <img :src="url">
        </div>
        <div>
          <p>流行排行榜</p>
          <p>作者：QQ音乐</p>
        </div>
      </div>
      <div class="dics-menu">
        <div>
          <i class="icon-music">&#xe680;</i>
          <div>收藏</div>
        </div>
        <div>
          <i class="icon-music">&#xe680;</i>
          <div>收藏</div>
        </div>
        <div>
          <i class="icon-music">&#xe680;</i>
          <div>收藏</div>
        </div>
        <div>
          <i class="icon-music">&#xe680;</i>
          <div>收藏</div>
        </div>
      </div>
    </div>
    <div class="music-content">
      <header>
        <i class="icon-music1">&#xe680;</i>
        <span>播放全部</span>
      </header>
      <div>
        <ul>
          <li v-for="(item, i) in content">
            <div class="music-item">
              <div>{{i+1}}</div>
              <div>{{item.data.songname}}</div>
              <div>
                <i class="icon-more">&#xe600;</i>
              </div>
            </div>
          </li>
        </ul>
      </div>
    </div>
    <musicPlaying></musicPlaying>
  </div>
</template>

<script>
  import musicPlaying from '@/components/public/musicPlaying.vue';
  import songList from '@/api/songList';

  export default {
    name: "song-list",
    components: {
      musicPlaying
    },
    data() {
      return {
        content: {},
        url: '',
        id: this.$route.params.id||4
      }
    },
    created(){
      let that = this;
      songList(this.id).then(function(data){
        that.content = data.songlist;
        that.url = data.topinfo.MacListPicUrl
      })
    },
    methods: {}
  }
</script>

<style scoped>
  .song-list {
  . padding-bottom: 2.5 rem;
  }

  .top {
    height: 12rem;
    color: #fff;
    background-color: #e5e4c6;
    box-sizing: border-box;
    padding: 3.5rem 1.1rem 1rem;
  }

  .list-header {
    padding: 0 0.5rem;
    width: 100%;
    height: 2.5rem;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 100;
    line-height: 2.5rem;
  }

  .list-header span {
    vertical-align: 9%;
    display: inline-block;
    width: 13.6rem;
    margin-left: .2rem;
  }

  .dics {
    overflow: hidden;
  }

  .dics > div {
    float: left;
  }

  .icon-back {
    display: inline-block;
    transform: rotate(90deg);
    font-size: 1.2rem;
  }

  .icon-search {
    font-size: 1.1rem;
  }

  .icon-more {
    font-size: 1.2rem;
  }

  .sheet-pic {
    width: 5rem;
    height: 5rem;
    margin-right: 0.5rem;
  }

  .sheet-pic > img {
    width: 100%;
    height: 100%;
  }

  .top > div {
    font-size: 14px;
    line-height: 2rem;
  }

  .dics-menu {
    line-height: .9rem !important;
    margin-top: .8rem;
  }

  .dics-menu > div {
    float: left;
    width: 25%;
    text-align: center;
    font-size: 12px
  }

  .music-content {
    font-size: 14px;
    color: #353535;
  }

  .music-content > header {
    padding-left: .8rem;
    height: 2.1rem;
    line-height: 2.1rem;
    border-bottom: 1px solid #EEEEEE;
  }

  .icon-music1 {
    font-size: .8rem;
    margin-right: .2rem
  }

  .music-item {
    overflow: hidden;
    border-bottom: 1px solid #eee;
  }

  .music-item > div {
    height: 2.5rem;
    width: 2.5rem;
    float: left;
    line-height: 2.5rem;
    text-align: center;
  }

  .music-item > div:nth-of-type(2) {
    text-align: left;
    width: calc(100% - 5rem);
  }
</style>



